import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import router from './router'
import fastclick from 'fastclick'
import '../src/assets/rem'
import store from './store'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

import vuetitle from 'vue-wechat-title'
import VueLazyload from 'vue-lazyload'

import './common/stylus/index.styl'

Vue.use(AlertPlugin)
Vue.use(ToastPlugin)
Vue.use(ConfirmPlugin)
Vue.use(vuetitle)



Vue.use(VueLazyload, {
loading: require('./common/image/default.jpg')
})

Vue.prototype.$msgbox = function (title, msg, onHideCb) {
this.$vux.alert.show({
title: title,
content: msg,
onShow () {
console.log('Plugin: I\'m showing')
},
onHide () {
onHideCb && onHideCb()
}
})
}











Vue.prototype.$eval = function (str) {
document.eval('(' + str + ')')
}

Vue.prototype.$msgboxhide = function () {
this.$vux.alert.hide()
}

Vue.prototype.$confirm = function (title, msg, onConfirm) {

this.$vux.confirm.show({
title: title,
content: msg,
onCancel () {
console.log('prompt cancel')
},
onConfirm () {
onConfirm && onConfirm()
}
})
}

Vue.prototype.$getCookie = function (cname) {
let name = cname + '='
let ca = document.cookie.split(';')
for (let i = 0; i < ca.length; i++) {
let c = ca[i].trim()
if (c.indexOf(name) === 0) return c.substring(name.length, c.length)
}
return ''
}

Vue.prototype.setTitle = function (t) {
document.title = t
var i = document.createElement('iframe')
i.src = '//m.baidu.com/favicon.ico'
i.style.display = 'none'
i.onload = function () {
setTimeout(function () {
i.remove()
}, 9)
}
document.body.appendChild(i)
}

Vue.config.productionTip = false

/* eslint-disable no-new */
fastclick.attach(document.body)

new Vue({
el: '#app',
router,
store,
render: h => h(App)
})



